.new-collections{
    margin-top: 40px;
    background-color: rgb(186, 201, 196);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
   margin-bottom: 100px;
   

}
.new-collections h1{
    
    color:rgb(235, 56, 56);
    font-size: 50px;
    font-weight: 600;
}


.new-collections hr {
    width:200px;
    border-width: 2px; 
     border-style: solid;
    font-weight: bold;
    height: 6px;
    border-radius: 10px;
    background: #252525;

}

.new-collections-item{
  
    margin-top: 50px;
    display: flex;
    gap:25px;
    margin-left: 50px;

}


.collections{
    
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top:50px;
    gap:30px;
    margin-left: 40px;
    

}

@media(max-width:1280px)
{
 
    .new-collections h1{
        font-size: 40px;
        padding-top: 10px;
    }

    .new-collections hr{
     width: 120px;
     height: 3px;
    }
    .collections{
        gap:15px;
        margin-top: 20px;
    }
}

@media(max-width:1024px)
{
   
    .new-collections h1{
        font-size: 30px;
    }

    .new-collections hr{
     width: 160px;
     height: 4px;
    }
    .collections{
        gap:20px;
        margin-top: 30px;
    }
}

@media(max-width:1024px)
{
    .new-collections{
        
        gap:6px;
    }
    .new-collections h1{
        font-size: 20px;
    }

    .new-collections hr{
     width: 100px;
    
    }
    .collections{
        gap:5px;
        
    }
}

@media(max-width:500px)
{
   
    .collections{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap:20px;
    }
  
}