
.productdisplay{
    display: flex;
    margin:0px 170px;

}

.productdisplay-left{
    display: flex;
    gap:17px;

}

.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap:16px;

}

.productdisplay-img-list img{
    height: 163px;
}

.productdisplay-main-img{
    width: 586px;
    height: 700px;
}
.productdisplay-right{
    margin:0px 70px;
    display: flex;
    flex-direction: column;

}

.productdisplay-right h1{
    color: rgb(61, 61, 61);
    font-size: 40px;
    font-weight:700px;

}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    margin-top:13px;
    gap:5px;
    color:rgb(28, 28, 28);
    font-size: 16px;
}

.productdisplay-right-prices{
    display: flex;
    margin:40px 0px;
    gap:30px;
    font-size: 24px;
    font-weight: 700px;
}

.productdisplay-right-prices-old{

    color: rgb(129, 129, 129);
    text-decoration: line-through;

}

.productdisplay-right-prices-new {
    color:red;
}

.productdisplay-right-sizes h1 {
    margin-top: 55px;
    color:rgb(101, 101, 101);
    font-size: 20px;
    font-weight: 600;

}
.productdisplay-right-size-options{
    display: flex;
    margin: 30px 0px;
    gap:20px;


}

.productdisplay-right-size-options div{

    padding: 18px 24px;
    background: rgb(251, 251, 251);
    border:1px solid rgb(235, 235, 235);
    border-radius: 3px;
    cursor: pointer;
}
.productdisplay-right-size-options div.selected {
    background-color: rgb(240, 193, 75);
    border-color: black;
}

.productdisplay-right button{
    padding: 20px 40px;
    width:200px;
    font-size: 16px;
    font-weight: 600;
    font-weight: bold;
    color:white;
    background: rgb(255, 0, 0);
    margin-bottom: 40px;
    border:none;
    outline:none;
    cursor:pointer;
}

.productdisplay-right-category{

    margin-top: 10px;
}
.productdisplay-right-category span{

    font-weight: 600;
}
.use{
    padding-left: 15px;
    width: 60%;
    margin-top: 10px;
    font-size: medium;
    margin-bottom: 80px;
}
.plants{
    padding-left: 15px;
    margin-top: 10px;
    font-size: medium;
    padding-bottom: 10px;
}
.use span{

    font-weight: 600;
}
.plants span{

    font-weight: 600;
}

@media(max-width:1280px)
{
    .productdisplay{
        margin: 0px 60px;
    }
    .productdisplay-left{
        gap:10px;

    }
    .productdisplay-img-list{
        gap:10px;
    }
    .productdisplay-img-list img{
        height: 120px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 510px;
    }
    .productdisplay-right{
        margin: 0px 30px;
    }
    .productdisplay-right h1{
        font-size: 22px;
    }
    .productdisplay-right-stars{
        gap:10px;
        font-size: 13px;
    }

    .productdisplay-right-prices{
        margin: 10px 0px;
        font-size: 18px;
    }
    .productdisplay-right-description{
        font-size:13px;

    }
    
    .use{
        padding-left: 10px;
        font-size:13px;
        margin-bottom: 80px;

    }
    .plants{
        padding-left: 10px;
        font-size:13px;

    }
    .productdisplay-right-size h1{
        margin-top:20px;
        font-size: 20px;

    }
    .productdisplay-right-sizes div{
        padding: 14px 20px;
    }
    .productdisplay-right button{
        width: 150px;
        padding: 15px 0px;
        margin-bottom: 20px;
    }
    .productdisplay-right-category {
        margin-top: 5px;
        font-size: 14px;
    }

}


@media(max-width:1024px)
{
    .productdisplay{
        margin: 0px 30px;
    }
    .productdisplay-left{
        gap:5px;

    }
  
    .productdisplay-img-list img{
        height: 80px;
    }
    .productdisplay-main-img{
       
        height: 350px;
    }

   
    .productdisplay-right h1{
        font-size: 18px;
    }
    .productdisplay-right-stars img{
       width: 15px;
    }

   
    .productdisplay-right-description{
        font-size:12px;

    }
    .use{
        padding-left: 10px;
        font-size:12px;
        margin-bottom: 70px;

    }
    .plants{
        padding-left: 10px;
        font-size:12px;

    }
    .productdisplay-right-size {
        margin:10px 0px;
       
    }
    .productdisplay-right-sizes div{
        padding: 10px 16px;
        font-size: 12px;
    }
    .productdisplay-right button{
        width: 120px;
        padding: 10px 2px;
        margin-bottom: 10px;
        font-size: 12px;
    }
    .productdisplay-right-category{
      
        font-size: 12px;
    }
    
}

@media(max-width:800px)
{
    .productdisplay{
        margin: 0px 14px;
    }
    .productdisplay-left{
        gap:0px;
        flex-direction: column-reverse;
        align-items: center;
        

    }

    .productdisplay-img-list {
       
        flex-direction: row;
        gap:10px;
        margin-bottom: 10px;
    }
    
  
    .productdisplay-img-list img{
        height: 70px;
        
       
    }
    .productdisplay-main-img{
       
        height: 300px;
    }

   
    .productdisplay-right h1{
        font-size: 14px;
    }
    .productdisplay-right-stars img{
       width: 13px;
    }

   
    .productdisplay-right-description{
        font-size:13px;

    }
    .use{
        padding-left: 10px;
        font-size:13px;
        margin-bottom: 70px;

    }
    .plants{
        padding-left: 10px;
        font-size:13px;

    }
    .productdisplay-right-size h1 {
        margin-top:10px 0px;
       
    }
    .productdisplay-right-sizes {
      margin-top: 10px;
        gap: 5px;
    }

    .productdisplay-right-sizes div{
      padding: 4px 11px;
        
      }
    .productdisplay-right button{
        width: 100px;
       
        font-size: 11px;
    }
    .productdisplay-right-category{
      
        font-size: 14px;
        margin-bottom: 10px;
    }
    
}

@media(max-width:500px)
{
    .productdisplay{
       flex-direction: column;
    }
    .productdisplay-left{
        gap:10px;
        flex-direction: row;
        margin: auto;

    }

    .productdisplay-img-list {
       
        flex-direction: column;
        gap:8px;
       
    }
    .plants{
        padding-left: 10px;
    }
    .use{
        padding-left: 10px;
        width: 100%;
    }
    
  
    .productdisplay-img-list img{
        height: 75px;
    }
    .productdisplay-main-img{
       
        height: 330px;
    }

   
    .productdisplay-right {
      margin: 5px;
    }
    .productdisplay-right h1 {
        margin-top: 15px;
        font-size: 20px;
        font-weight: 500;
      }

    .productdisplay-right-stars img{
       width: 15px;
    }

   
    .productdisplay-right-sizes{
      gap:10px;
      margin: 20px 0px;

    }
   
    .productdisplay-right-sizes div {
      padding: 10px 15px;
        font-size: 14px;
    }

    
    .productdisplay-right button{
        width: 130px;
       
        font-size: 15px;
        padding: 12px 0px;
    }
    .productdisplay-right-category{
      
        font-size: 16px;
    }
    
}