.navbar {
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow:0 1px 3px -2px black ;
    align-items: center;
}

.nav-logo {
    display: flex;
    align-items: center;
    gap :10px

}

.nav-logo p {

    color:green;
    font-size:38px ;
    font-weight: 700;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color:brown;
    font-weight: 600;
    font-weight: bolder;

}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor:pointer;

}

.nav-menu hr {

    border:none ;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: rgb(165, 15, 15);

}

.nav-login-cart{

    display: flex;
    align-items: center;
    gap: 45px;

}

.nav-login-cart button {
    width: 130px;
    height: 50px;
    outline: none;
    border :1px solid rgb(86, 84, 84);
    border-radius: 75px;
    color: darkslategrey;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
    font-weight: bold;
   

}

.nav-login-cart button:active {
    background-color:red;

}
.nav-cart-count{
  width:22px;
  height:22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -35px;
  margin-left: -55px;
  border-radius: 11px;

  font-size: 14px;
  background:red;
  color: aliceblue;


}
.nav-dropdown{
    display: none;
}

@media(max-width:1280px)
{
    .navbar{
        padding:12px 50px;

    }
    .nav-logo img{
        width: 40px;
    }

    .nav-logo p{
        font-size: 25px;
    }
    .nav-menu{
        gap:30px;
        font-size: 16px;
    }

    .nav-login-cart{
        gap:30px;

    }
    .nav-cart-cart button {
        width: 120px;
        height: 45px;
        font-size: 16px;

    }
   .nav-cart-count{
    margin-left:-40px;
    font-size: 12px;
   } 

   
}


@media(max-width:1024px)
{
    .navbar{
        padding: 12px 30px;
    }

    .nav-menu{
        gap:25px;
        font-size: 14px;
    }

    .nav-login-cart button{
        width: 80px;
        height: 35px;
        font-size:14px;
    }

    .nav-cart-cart img{
        width:30px;
    }

    .nav-cart-count{
        width:18px;
        height: 18px;
    }


}

@media(max-width:800px)
{
    .navbar{
        padding: 10px 0px;

    }
    .nav-dropdown{
   display: block;
   width: 30px;
   rotate: -90deg;
   transition: 0.5s;
    }

    .nav-menu{
        display: none;
        height: 80px;
        width:100%;
        position:absolute;
        background-color:white;
        justify-content: center;
        top :50px;
    }
    .nav-menu-visible{
        display: flex;

    }
    .nav-dropdown.open{
       transform: rotate(90deg);

    }
}

@media(max-width:500px)
{
    .navbar{
       
        padding:8px 0px;
        gap:0;
    }
    .nav-logo{
        transform: scale(0.5);
    }

    .nav-menu{
      
        height: 100px;
        top:40px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    }
}

@media(max-width:374px){

    .nav-menu{
        font-size: small;
       height: 100px;
        top :80px;
        width: 90%;
    }
}
